<template>
    <div class="box">
        <Header :got="true" :cmt="true" :amt="false" :aot="false" :ant="false" :gnt="false"  :cot="false"></Header>
        <div class="content">
            <div class="item">
                <div class="item-1">
                    <img src="../images/2.jpg" alt="">
                </div>
                <div class="item-2">
                    <div class="wait">
                        <div>
                            <p><b>效果演示<van-icon class="arrow" name="arrow" /></b></p>
                            <p>2019-9-08 18:39</p>
                        </div>
                        <div>
                            等待支付
                        </div>
                    </div>
                    <div class="ming">
                        <ul>
                            <li>的味道切尔奇二群翁</li>
                            <li><b>￥855.00</b></li>
                        </ul>
                    </div>
                    <div class="but">
                        <div>去支付(还剩14分50秒)</div>
                    </div>
                </div>
            </div>
            <div class="item">
                <div class="item-1">
                    <img src="../images/2.jpg" alt="">
                </div>
                <div class="item-2">
                    <div class="wait">
                        <div>
                            <p><b>效果演示<van-icon class="arrow" name="arrow" /></b></p>
                            <p>2019-9-08 18:39</p>
                        </div>
                        <div>
                            等待支付
                        </div>
                    </div>
                    <div class="ming">
                        <ul>
                            <li>的味道切尔奇二群翁</li>
                            <li><b>￥855.00</b></li>
                        </ul>
                    </div>
                    <div class="but">
                        <div>去支付(还剩14分50秒)</div>
                    </div>
                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
    import Header from '../header/Header'
    import Footer from '../footer/Footer'
    export default {
        components: {
            Header,
            Footer
        }
    }
</script>

<style lang="scss" scoped>
.but{
    width: 100%;
    height: 0.8rem;
    padding:0.25rem;
    div{
        width: 3.3rem;
        height: 0.45rem;
        color: rgb(202, 202, 98);
        border: none;
        outline: none;
        background: white;
        border: 1px solid rgb(202, 202, 98);
        text-align: center;
        margin-left: 2.2rem;
    }

}
.ming{
    width: 100%;
    border-bottom: 1px solid rgb(224, 216, 216);
    ul{
        width: 100%;
        height: 1rem;
        display: flex;
        line-height: 1rem;
        li:nth-of-type(1){
            flex: 2;
        }
        li:nth-of-type(2){
            flex: 1;
            text-align: center;
            color: rgb(169, 209, 87);
        }
    }
}
    .item{
        width: 100%;
        height: 3.5rem;
        border-bottom: 1px solid gray;
        display: flex;
        border-bottom: 0.2rem solid rgb(245, 242, 242);
        .item-1{
            flex: 1;
            padding: 0.25rem;
            // background: wheat;
            img{
                width: 1.5rem;
                height: 1.5rem;
            }
        }
        .item-2{
            flex: 4;
            font-size: 0.3rem;
            .wait{
                width: 100%;
                height: 1.3rem;
                border-bottom: 1px solid rgb(224, 216, 216);
                display: flex;
                div:nth-of-type(1){
                    flex: 2;
                    p:nth-of-type(1){
                        font-size: 0.35rem;
                        padding-top: 0.23rem;
                        .arrow{
                            color: gray;
                            font-size: 0.25rem;
                        }
                    }
                    p:nth-of-type(2){
                        font-size: 0.28rem;
                        padding-top: 0.1rem;
                    }
                }
                div:nth-of-type(2){
                    flex: 1;
                    line-height: 1rem;
                    // padding-top: 0.25rem;
                    text-align: center;
                }
            }
        }
    }
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
    }
</style>


